import { MessageBot } from '@bhmb/bot';

var page = "<header class=\"header is-fixed-top\">\r\n    <nav class=\"navbar is-primary\" role=\"navigation\" aria-label=\"main navigation\">\r\n        <div class=\"navbar-brand\">\r\n            <div class=\"navbar-item nav-slider-toggle\">\r\n                <img src=\"https://gitcdn.xyz/cdn/Blockheads-Messagebot/UI/master/logo.png\">\r\n            </div>\r\n            <a class=\"navbar-item nav-slider-toggle\">Menu</a>\r\n        </div>\r\n    </nav>\r\n</header>\r\n\r\n<div class=\"nav-slider-container\">\r\n    <nav class=\"nav-slider\"></nav>\r\n    <div class=\"is-overlay nav-slider-toggle\"></div>\r\n</div>\r\n\r\n<div id=\"container\" class=\"has-fixed-nav\"></div>\r\n\r\n\r\n<div id=\"modal\" class=\"modal\">\r\n    <div class=\"modal-background\"></div>\r\n    <div class=\"modal-card\">\r\n        <header class=\"modal-card-head\"></header>\r\n        <section class=\"modal-card-body\"></section>\r\n        <footer class=\"modal-card-foot\"></footer>\r\n    </div>\r\n</div>\r\n";

function api () {
    const menuSlider = document.querySelector('.nav-slider-container .nav-slider');
    const toggleMenu = () => menuSlider.classList.toggle('is-active');
    for (let el of document.querySelectorAll('.nav-slider-toggle')) {
        el.addEventListener('click', toggleMenu);
    }
    const tabs = new Map();
    const groups = new Map();
    const container = document.getElementById('container');
    const menuContainer = document.querySelector('.nav-slider');
    menuContainer.addEventListener('click', event => {
        let nav = event.target;
        let tab = tabs.get(nav);
        if (tab) {
            // Containers
            Array.from(container.children).forEach(child => child.classList.remove('visible'));
            tab.classList.add('visible');
            // Nav items
            Array.from(menuContainer.querySelectorAll('span.nav-item')).forEach(span => span.classList.remove('is-active'));
            nav.classList.add('is-active');
        }
    });
    const addTab = (text, groupName) => {
        let div = container.appendChild(document.createElement('div'));
        let parent = menuContainer;
        if (groupName)
            parent = groups.get(groupName);
        let nav = parent.appendChild(document.createElement('span'));
        nav.textContent = text;
        nav.classList.add('nav-item');
        tabs.set(nav, div);
        return div;
    };
    const removeTab = (content) => {
        for (let [nav, div] of tabs.entries()) {
            if (div == content) {
                div.remove();
                nav.remove();
                tabs.delete(nav);
                return;
            }
        }
    };
    const addTabGroup = (text, groupName, parent) => {
        let details = groups.get(groupName);
        if (details) {
            details.children[0].textContent = text;
            return;
        }
        let parentElement = menuContainer;
        if (parent)
            parentElement = groups.get(parent);
        details = parentElement.appendChild(document.createElement('details'));
        details.classList.add('nav-item');
        let summary = details.appendChild(document.createElement('summary'));
        summary.textContent = text;
        groups.set(groupName, details);
    };
    const removeTabGroup = (groupName) => {
        let group = groups.get(groupName);
        if (!group)
            return;
        for (let child of group.querySelectorAll('span')) {
            // Unless someone has been purposely messing with the page, this is a safe assertion
            removeTab(tabs.get(child));
        }
        groups.delete(groupName);
        group.remove();
    };
    const handleRule = (rule, element) => {
        if (typeof rule.text == 'string') {
            element.textContent = rule.text;
        }
        else if (typeof rule.html == 'string') {
            element.innerHTML = rule.html;
        }
        let blacklist = ['text', 'html', 'selector'];
        if (element instanceof HTMLTextAreaElement && 'value' in rule) {
            element.textContent = rule.value;
            blacklist.push('value');
        }
        //See https://github.com/Blockheads-Messagebot/MessageBot/issues/52
        if (element instanceof HTMLSelectElement && 'value' in rule) {
            let child = element.querySelector(`[value="${rule.value}"]`);
            if (child)
                child.selected = true;
        }
        Object.keys(rule)
            .filter(key => !blacklist.includes(key))
            .forEach(key => element.setAttribute(key, rule[key]));
    };
    const buildTemplate = (template, target, rules) => {
        if (typeof template == 'string')
            template = document.querySelector(template);
        if (typeof target == 'string')
            target = document.querySelector(target);
        let parent = document.importNode(template.content, true);
        for (let rule of rules) {
            let element = parent.querySelector(rule.selector);
            if (!element) {
                console.warn(`Could not find ${rule.selector}`, rule);
                continue;
            }
            handleRule(rule, element);
        }
        target.appendChild(parent);
    };
    const notify = (text, displayTime = 3) => {
        let el = document.body.appendChild(document.createElement('div'));
        el.classList.add('bot-notification', 'is-active');
        el.textContent = text;
        let timeouts = [
            setTimeout(() => el.classList.remove('is-active'), displayTime * 1000),
            setTimeout(() => el.remove(), (displayTime + 1) * 1000)
        ];
        el.addEventListener('click', () => {
            timeouts.forEach(clearTimeout);
            el.remove();
        });
    };
    let alertInstance = {
        active: false,
        queue: []
    };
    const modal = document.getElementById('modal');
    const modalBody = modal.querySelector('.modal-card-body');
    const modalFooter = modal.querySelector('.modal-card-foot');
    const addButton = (button) => {
        let el = modalFooter.appendChild(document.createElement('a'));
        let styles = ['button'];
        if (typeof button == 'object') {
            if (button.style)
                styles.push(button.style);
            el.textContent = button.text;
        }
        else {
            el.textContent = button;
        }
        el.classList.add(...styles);
    };
    const showAlert = () => {
        alertInstance.active = true;
        const { html, buttons, callback } = alertInstance.queue.shift();
        modalBody.innerHTML = html;
        Array.isArray(buttons) ? buttons.forEach(addButton) : addButton('OK');
        modal.classList.add('is-active');
        modalFooter.addEventListener('click', function buttonHandler(event) {
            let target = event.target;
            if (target.tagName != 'A')
                return;
            modal.classList.remove('is-active');
            try {
                if (callback)
                    callback.call(null, target.textContent);
            }
            catch (err) {
                console.error('Error calling alert callback', err);
            }
            modalFooter.innerHTML = '';
            modalFooter.removeEventListener('click', buttonHandler);
            alertInstance.active = false;
            if (alertInstance.queue.length)
                showAlert();
        });
    };
    const alert = (html, buttons, callback) => {
        alertInstance.queue.push({ html, buttons, callback });
        if (!alertInstance.active)
            showAlert();
    };
    const prompt = (text, callback) => {
        let p = document.createElement('p');
        p.textContent = text;
        alert(p.outerHTML + `<textarea class="textarea"></textarea>`, ['OK', 'Cancel'], () => {
            let el = modalBody.querySelector('textarea');
            if (callback)
                callback(el.value || '');
        });
    };
    return {
        toggleMenu,
        addTab,
        removeTab,
        addTabGroup,
        removeTabGroup,
        buildTemplate,
        notify,
        alert,
        prompt
    };
}

function polyfill() {
    if (!('open' in document.createElement('details'))) {
        let style = document.createElement('style');
        style.textContent += `details:not([open]) > :not(summary) { display: none !important } details > summary:before { content: "â–¶" display: inline-block font-size: .8em width: 1.5em font-family:"Courier New" } details[open] > summary:before { transform: rotate(90deg) }`;
        document.head.appendChild(style);
        window.addEventListener('click', function (event) {
            let target = event.target;
            if (target.tagName == 'SUMMARY') {
                let details = target.parentNode;
                if (!details) {
                    return;
                }
                if (details.getAttribute('open')) {
                    details.open = false;
                    details.removeAttribute('open');
                }
                else {
                    details.open = true;
                    details.setAttribute('open', 'open');
                }
            }
        });
    }
}

MessageBot.registerExtension('ui', function (ex) {
    if (typeof document == 'undefined') {
        throw new Error('This extension cannot be loaded outside of a browser environment.');
    }
    ex.uninstall = function () {
        throw new Error('The UI extension cannot be removed once loaded');
    };
    // Page creation
    document.body.innerHTML = page;
    document.head.querySelectorAll('link').forEach(el => el.remove());
    let style = document.head.appendChild(document.createElement('link'));
    style.rel = 'stylesheet';
    style.href = 'https://gitcdn.xyz/cdn/Blockheads-Messagebot/UI/master/index.css';
    polyfill();
    // Expose api
    ex.exports = api();
});
